/* Component styles are defined here */

:root {
    --blue: rgb(111, 195, 223);
    --orange: rgb(223, 116, 12);
    --yellow: rgb(244, 175, 45);
    --darkblue: rgb(27, 45, 101);
}

.turn-header {
    font-size: 5rem;
    text-align: center;
}

.board {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    border-collapse: collapse;
    padding: 0.6rem 0;
    gap: 1rem;
}

.game-square {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 12.5rem;
    height: 12.5rem;
    font-size: 8rem;
    background-color: transparent;
    border: 4px solid var(--yellow);
    border-radius: 10px;
    cursor: pointer;
    transition: transform 0.1s ease-out;
}

.game-square:hover {
    background-color: var(--darkblue);
}

.game-square:active {
    transform: scale(0.8);
}

.reset {
    display: block;
    margin: 0 auto;
    padding: 1rem 3rem;
    background-color: transparent;
    color: var(--yellow);
    font-size: 3rem;
    font-weight: bold;
    border-radius: 1.2rem;
    border: 4px solid var(--blue);
    cursor: pointer;
    transition: transform 0.1s ease-out;
}

.reset:hover {
    background-color: var(--darkblue);
}

.reset:active {
    transform: scale(0.8);
}

.score-panel {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    padding: 0px 0 20px 0;
    row-gap: 20px;
}

.score-text {
    font-size: 3rem;
    font-weight: bold;
    letter-spacing: 1.5px;
    place-self: center;
    text-align: center;
}

.score-button {
    grid-column: 1 / 3;
    place-self: center;
    padding: 1rem 2rem;
    color: var(--yellow);
    font-size: 2rem;
    font-weight: bold;
    border: 3px solid var(--blue);
    background-color: transparent;
    color: var(--orange);
    border-radius: 50px;
    cursor: pointer;
    transition: transform 0.1s ease-out;
}

.score-button:hover {
    background-color: var(--darkblue);
}

.score-button:active {
    transform: scale(0.8);
}

.endgame-message {
    display: inline-block;
    position: absolute;
    top: 20%;
    left: 50%;
    font-size: 10rem;
    text-align: center;
    transform: translate(-50%, -50%);
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
    background-clip: text;
    color: transparent;
}

@media (min-width: 1370px) {
    .board {
        padding: 0.6rem 0 1.9rem 0;
    }

    .score-panel {
        position: absolute;
        top: 50%;
        left: 5%;
        transform: translateY(-50%);
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: repeat(2, 1fr);
        justify-content: center;
        align-items: center;
        padding: 20px;
        row-gap: 20px;
    }

    .score-button {
        grid-column: 1 / 1;
    }
}