/* Main document styles are defined here */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 100vh;
    background-color: #252525;
    font-family: system-ui;
    user-select: none;
}

@media (max-width: 650px) {
    html {
        font-size: 10px;
    }
}